
body {
  background-color: var(--bcg-main);
  font-family: var(--font-primary), var(--font-primary-rezerv);
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
  padding: 0px;
  margin: 0;
}
h1,
h2,
h3,
p {
  margin: 0 0 0 0;
}
img {
  width: 100%;
  display: block;
  height: auto;
}
button {
  display: block;
  padding: 0;
}


